<template>
  <div class="orderDetail mescroll">
    <div class="address flex" v-if="detail.orderType != 7">
      <img src="../../assets/img/icon/dizhi.png" class="icon" />
      <div class="flex-2">
        <div class="personalInfo">
          <span class="name">收货人：{{detail.userName2}}</span>
          <span class="tenlo">{{detail.userMobile}}</span>
        </div>
        <div class="addressInfo">{{detail.addrAddr}}</div>
      </div>
    </div>
    <div class="address flex" v-else>
      <img src="../../assets/img/icon/dizhi.png" class="icon" />
      <div class="flex-2">
        <div class="personalInfo">
          <span class="name">提货人：{{detail.userName2}}</span>
          <span class="tenlo">{{detail.userMobile}}</span>
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="goodsInfo">
      <div class="goodsInfo-top flex">
        <div class="flex-2 title">商品信息</div>
        <div class="status" :style="detail.orderStatus == 1 || detail.orderStatus == 2?'color:#eb5252':detail.orderStatus == 3?'color:#333':'color:#999'">{{$utils.getText(detail.orderStatus)}}</div>
      </div>
      <div class="list">
        <div class="item flex" v-for="(item,index) in detail.scEcomOrdersDetailList" :key="index">
          <div class="item-pic flex-1">
            <img :src="item.scEcomGoods?$utils.dealImg(item.scEcomGoods.gimg):''" />
          </div>
          <div class="flex-2">
            <div class="title">{{item.scEcomGoods?item.scEcomGoods.gname:''}}</div>
            <div class="price">
              ¥
              <span class="money">{{item.scEcomGoods?item.scEcomGoods.goutPrice.toFixed(2):'0.00'}}</span>
              <span class="num">×{{item.gorderCount}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="goodsInfo-bottom">
        <div class="flex1" v-if="detail.remarks">
          <div class="flex-1">备注</div>
          <div class="flex-2">{{detail.remarks}}</div>
        </div>
        <div class="line1"></div>
        <div class="flex">
          <div class="flex-1">商品数量</div>
          <div class="flex-2">{{detail.shopCount}}</div>
        </div>
        <div class="flex">
          <div class="flex-1">商品合计</div>
          <div class="flex-2">￥{{detail.oorderMoney?detail.oorderMoney.toFixed(2):'0.00'}}</div>
        </div>
        <div class="flex" v-if="detail.oorderYunFei">
          <div class="flex-1">运费合计</div>
          <div class="flex-2">￥{{detail.oorderYunFei.toFixed(2)}}</div>
        </div>
        <div class="flex" v-if="detail.oorderYouhui">
          <div class="flex-1">优惠合计</div>
          <div class="flex-2">￥{{detail.oorderYouhui.toFixed(2)}}</div>
        </div>
        <div class="flex">
          <div class="flex-1">实付款</div>
          <div class="flex-2 allPrice">
            ￥
            <span>{{detail.oorderMoneyShifu?detail.oorderMoneyShifu.toFixed(2):'0.00'}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="orderInfo" :style="detail.orderStatus == 2?'padding-bottom:70px':''">
      <div class="title">订单信息</div>
      <div class="flex">
        <div class="flex-1">订单编号</div>
        <div class="flex-2">{{detail.orderno}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">物流方式</div>
        <div class="flex-2">{{detail.sname}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">下单时间</div>
        <div class="flex-2">{{detail.createDate}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus != 1 && detail.orderStatus != 11">
        <div class="flex-1">支付方式</div>
        <div class="flex-2">{{detail.pay_type == 1?'余额':'微信'}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus != 1 && detail.orderStatus != 11">
        <div class="flex-1">支付时间</div>
        <div class="flex-2">{{detail.opayWdate?detail.opayWdate.substring(0,16):''}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus == 3 || detail.orderStatus == 4">
        <div class="flex-1">发货方式</div>
        <div class="flex-2">{{detail.comName}}</div>
      </div>
      <div class="flex" v-if="(detail.orderStatus == 3 || detail.orderStatus == 4) && detail.hlBillno">
        <div class="flex-1">快递单号</div>
        <div class="flex-2">{{detail.hlBillno}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus == 3 || detail.orderStatus == 4">
        <div class="flex-1">发货时间</div>
        <div class="flex-2">{{getTimeText(detail.oHuoDate)}}</div>
      </div>
      <div class="flex" v-if="(detail.orderStatus == 3 || detail.orderStatus == 4) && detail.hlMsg">
        <div class="flex-1">备注信息</div>
        <div class="flex-2">{{detail.hlMsg}}</div>
      </div>
    </div>
    <div class="bottom" v-if="detail.orderStatus  == 2">
      <div class="btns flex">
        <div class="btn btnPay" v-if="detail.orderStatus  == 2" @click="show = true">发货</div>
      </div>
    </div>
    <van-popup v-model="show">
      <div class="popup">
        <div class="popup-mid">
          <van-cell-group>
            <van-cell title="物流公司" is-link :value="company||'请选择'" @click="companyShow = true" />
            <van-field v-model="number" input-align="right" clearable label="物流单号" placeholder="请输入" @blur="blur()" />
            <van-field :rows="3" v-model="remark" input-align="right" label="详细信息" placeholder="请输入" type="textarea" clearable maxlength="50" show-word-limit @blur="blur()" />
          </van-cell-group>
        </div>
        <div class="popup-bottom">
          <div class="btn" @click="submit">确认发货</div>
        </div>
      </div>
    </van-popup>
    <van-popup v-model="companyShow" position="bottom">
      <van-picker show-toolbar title="物流公司" :columns="companyList" value-key="comName" @cancel="companyShow = false" @confirm="onConfirm" />
    </van-popup>
  </div>
</template>
<script>
import { Popup, Picker, CellGroup, Cell, Field } from "vant";
import { getOrderDetail1, deliveryGoods, getCompanyList } from "@/api";
export default {
  components: {
    [Picker.name]: Picker,
    [Popup.name]: Popup,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
  },
  data() {
    return {
      show: false,
      company: "",
      companyId: "",
      number: "",
      remark: "",
      companyShow: false,
      companyList: [],
      detail: {},
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    getTimeText(value) {
      let year = new Date(value).getFullYear(),
        month = new Date(value).getMonth() + 1,
        day = new Date(value).getDate(),
        hour = new Date(value).getHours(),
        min = new Date(value).getMinutes(),
        sec = new Date(value).getSeconds();
      return (
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day) +
        " " +
        (hour < 10 ? "0" + hour : hour) +
        ":" +
        (min < 10 ? "0" + min : min)
      );
    },
    onConfirm(value, index) {
      this.company = value.comName;
      this.companyId = value.id;
      this.companyShow = false;
    },
    submit() {
      if (!this.companyId) {
        this.$toast("请选择物流公司");
        return;
      }
      if (this.company != "上门自提" && !this.number) {
        this.$toast("请输入物流单号");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      deliveryGoods({
        orderno: this.detail.id,
        hlCompany: this.companyId,
        hlBillno: this.number,
        hlMsg: this.remark,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast("已发货");
          this.show = false;
          this.loadData();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getOrderDetail1({
        id: this.$route.query.id,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data;
        } else {
          this.$toast(res.msg);
        }
      });
      getCompanyList().then(res => {
        if (res.code == 0) {
          this.companyList = res.data;
        } else {
          this.$toast(res.msg);
        }
      });
    },
  },
  mounted() {
    document.title = "订单详情";
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.orderDetail {
  background: #fff;
  .line {
    height: 5px;
    background: #efefef;
  }
  .line1 {
    border-top: 1pt solid #efefef;
    margin: 20px 0 5px;
  }
  .flex {
    display: flex;
    align-items: center;
    .flex-1 {
      flex: 1;
    }
    .flex-2 {
      flex: 2;
      position: relative;
      height: 100%;
    }
  }
  .address {
    padding: 15px;
    .icon {
      width: 20px;
      margin-right: 10px;
    }

    .flex-1 {
      flex: 1;
    }
    .flex-2 {
      flex: 2;
      font-size: 15px;
    }

    .personalInfo {
      font-size: 16px;
      line-height: 15px;
      font-weight: bold;
      .name {
        margin-right: 10px;
      }
      .tenlo {
        float: right;
      }
    }
    .addressInfo {
      font-size: 14px;
      color: #999;
      margin-top: 10px;
      line-height: 20px;
      .arrow {
        margin-top: 10px;
      }
    }
  }
  .goodsInfo {
    padding: 0 15px;
    margin-top: 10px;
    .goodsInfo-top {
      align-items: center;
      .title {
        font-size: 18px;
        font-weight: bold;
        line-height: 25px;
      }
      .status {
        font-size: 15px;
        line-height: 25px;
        color: $base-color;
      }
    }
    .list {
      .item {
        height: 120px;
        overflow: hidden;
        margin: 20px 0;
        align-items: flex-start;
        .item-pic {
          margin-right: 10px;
          img {
            width: 100%;
            height: 120px;
          }
        }
        .title {
          font-size: 16px;
          line-height: 25px;
          max-height: 50px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .parameter {
          color: #999;
          font-size: 15px;
          line-height: 20px;
          margin: 10px 0;
        }
        .price {
          font-size: 13px;
          font-weight: 600;
          color: #333;
          flex: 2;
          position: absolute;
          width: 100%;
          bottom: 10px;
          .money {
            font-size: 16px;
          }
          .num {
            color: #999;
            margin-left: 10px;
          }
        }
      }
    }
    .goodsInfo-bottom {
      padding: 10px 0;
      .flex {
        padding: 5px 0;
        line-height: 25px;
        color: #999;
        font-size: 16px;
        display: flex;
        align-items: center;
        .flex-2 {
          text-align: right;
          color: #333;
          flex: 2;
        }
        .flex-1 {
          flex: 1;
        }
      }
      .flex1 {
        padding: 5px 0;
        line-height: 25px;
        color: #999;
        font-size: 16px;
        display: flex;
        .flex-2 {
          text-align: right;
          color: #333;
          flex: 2;
        }
        .flex-1 {
          flex: 1;
        }
      }
      .allPrice {
        text-align: right;
        font-size: 20px;
        font-weight: bold;
        span {
          font-size: 30px;
          font-weight: bold;
        }
      }
    }
  }
  .orderInfo {
    padding: 10px 15px 20px;
    .title {
      font-size: 18px;
      font-weight: bold;
      line-height: 25px;
      margin: 0 0 10px;
    }
    .flex {
      line-height: 30px;
      color: #999;
      font-size: 16px;
      display: flex;
      .flex-1 {
        flex: 1;
      }
      .flex-2 {
        text-align: right;
        color: #333;
        flex: 2;
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 50px;
    background: #fff;
    width: 100%;
    box-shadow: 0 -2px 10px 1px rgba(0, 0, 0, 0.1);
    padding: 5px 0;
    .btns {
      padding: 0 15px;
      justify-content: flex-end;
      margin-top: 9px;
      .btn {
        width: 100px;
        color: #666;
        border: 1pt solid #f4f4f4;
        float: right;
        height: 30px;
        line-height: 30px;
        border-radius: 5px;
        font-size: 13px;
        text-align: center;
        margin-left: 10px;
      }
      .btnPay {
        background: $base-color;
        color: #fff;
        border: 1pt solid $base-color;
      }
    }
  }

  .van-popup {
    border-radius: 5px;
  }
  .popup {
    background: #fff;
    width: 85vw;
    height: 65vh;
    position: relative;
    &-top {
      padding: 10px;
      display: flex;
      img {
        width: 70px;
        height: 70px;
        border-right: 5px;
        margin-right: 10px;
      }
      .right {
        flex: 2;
        .name {
          line-height: 20px;
          height: 40px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          text-align: justify;
          word-break: break-all;
        }
        .price {
          margin: 10px 0 0;
          span {
            color: #ee0a24;
            margin-right: 5px;
          }
          del {
            color: #999;
            font-size: 13px;
          }
        }
      }
    }
    &-mid {
      .van-cell {
        padding: 10px;
      }
    }
    &-bottom {
      padding: 10px;
      position: absolute;
      width: calc(100% - 20px);
      height: 45px;
      bottom: 0;
      left: 0;
      .btn {
        width: 100%;
        line-height: 45px;
        text-align: center;
        background: #ee0a24;
        color: #fff;
